.copyright {
  display: flex;
  background: $light-grey 320px 300px;
  padding: 6px 13px 15px 19px;
}

.copyright__text {
  font-family: $arial;
  font-size: 16px;
  color: $dark-grey;
  display: inline-block;
  margin-top: 10px;
}

.copyright__logo {
  width: 41px;
  height: 41px;
  background: url("../img/htmlacademy.svg") no-repeat;
  display: inline-block;
  margin-left: auto;
}

@media (min-width: $tablet-width) {
  .copyright {
    margin: 0;
    padding: 0;
  }

  .copyright__text {
    margin-right: 15px;
    width: 114px;
  }
}

